.App {
  margin-top: 15px; }

.App .navbar-brand {
  font-weight: bold; }

.App .navbar-default {
  border-color: transparent;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#d80f40), color-stop(#cb0f41), color-stop(#bd1042), color-stop(#b01242), to(#a21441));
  background-image: -webkit-linear-gradient(left bottom, #d80f40, #cb0f41, #bd1042, #b01242, #a21441);
  background-image: -o-linear-gradient(left bottom, #d80f40, #cb0f41, #bd1042, #b01242, #a21441);
  background-image: linear-gradient(to right top, #d80f40, #cb0f41, #bd1042, #b01242, #a21441); }

.App .navbar-default .navbar-nav > li > a,
.App .navbar-default .navbar-brand {
  color: #ffffff; }

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:focus,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > li > a:focus,
.navbar-default .navbar-nav > li > a:hover {
  color: #ffffff;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#19255e), color-stop(#19245c), color-stop(#19235a), color-stop(#182358), to(#182256));
  background-image: -webkit-linear-gradient(left bottom, #19255e, #19245c, #19235a, #182358, #182256);
  background-image: -o-linear-gradient(left bottom, #19255e, #19245c, #19235a, #182358, #182256);
  background-image: linear-gradient(to right top, #19255e, #19245c, #19235a, #182358, #182256); }

.navbar {
  border: none; }

.navbar-fixed-bottom {
  margin: 15px; }



