.social-icons img {
  height: 25px;
  margin: 10px; }

h1 {
  font-weight: 800px;
  background: -webkit-gradient(linear, left bottom, right top, from(#d80f40), color-stop(#cb0f41), color-stop(#bd1042), color-stop(#b01242), to(#a21441));
  background: -webkit-linear-gradient(left bottom, #d80f40, #cb0f41, #bd1042, #b01242, #a21441);
  background: -o-linear-gradient(left bottom, #d80f40, #cb0f41, #bd1042, #b01242, #a21441);
  background: linear-gradient(to right top, #d80f40, #cb0f41, #bd1042, #b01242, #a21441);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.about-me {
  margin: 20px 0; }

.social-icons {
  border-radius: 4px;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#19255e), color-stop(#19245c), color-stop(#19235a), color-stop(#182358), to(#182256));
  background-image: -webkit-linear-gradient(left bottom, #19255e, #19245c, #19235a, #182358, #182256);
  background-image: -o-linear-gradient(left bottom, #19255e, #19245c, #19235a, #182358, #182256);
  background-image: linear-gradient(to right top, #19255e, #19245c, #19235a, #182358, #182256); }



